body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button-wrapper {
    height: 4em;
    display : flex; 
    justify-content: center; 
    align-items: center; 
    font-weight: light; 
    font-size: 1.4em; 
    background-color: black;
    color: #888;
    flex: 1 1; 
    outline: 1px solid #888; 
}

.operator{ 
    background-color: #fe9241;
    color: white; 

}
.input {
    height: 4em;
    display: flex; 
    justify-content: flex-end; 
    align-items: center;
    font-weight: bolder;
    font-size: 1.4em;
    background-color: #ffffff;
    color: #000000;
    padding: 0.5em;
    outline: 1px solid #e0e1e6;
}


.clearBtn{
    height: 4em;
    display: flex; 
    justify-content: center; 
    align-items: center; 
    font-weight: light; 
    font-size: 1.4em;
    background-color: #888888;
    color: #000000;
    flex: 1 1; 
    outline: 1px solid #888; 
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%
}

.calc-wrapper{
  width: 400px;
  height: 600px;
}

.row{
  display: flex; 
  width: 100%
}

